import { useNavigate, useParams } from 'react-router-dom';
import { t } from '../../../helpers/i18n/dictionary';
import useConnection from '../../../hooks/connection/useConnection';
import useContact from '../../../hooks/contact/useContact';
import useImage from '../../../hooks/media/useImage';
import { ContactConfig } from '../../../provider/contact/ContactTypes';
import Alert from '../../Alerts/Alert/Alert';
import ActionButton from '../../Buttons/ActionButton';
import FallbackImg from '../../FallbackImg/FallbackImg';
import AddressBook from '../../Icons/AddressBook/AddressBook';
import Cake from '../../Icons/Cake/Cake';
import House from '../../Icons/House/House';
import Person from '../../Icons/Person/Person';
import Phone from '../../Icons/Phone/Phone';
import PageMeta from '../../Layout/PageMeta/PageMeta';
import Section from '../../Sections/Section';
import { ConnectionPermissionViewer } from '../ConnectionDetails/ConnectionDetails';

const ContactDetails = () => {
  const { contactKey } = useParams();
  const navigate = useNavigate();

  const {
    fetch: { data: contact, isLoading: isContactsLoading },
    refresh: { mutate: refresh, status: refreshState },
  } = useContact({ id: contactKey });

  const { data: connectionInfo } = useConnection({ dotYouId: contact?.dotYouId }).fetch;
  const { data: imageUrl } = useImage(
    contact?.imageFileId || undefined,
    ContactConfig.ContactTargetDrive
  ).fetch;

  const fullName = `${contact?.name?.givenName} ${contact?.name?.surname}`;

  return (
    <>
      <PageMeta
        icon={AddressBook}
        title={
          <span className="flex flex-col">
            <span className="block">{fullName}</span>
            <small className="text-sm">{contact?.dotYouId}</small>
          </span>
        }
        breadCrumbs={[{ href: '/owner/contacts', title: 'Contacts' }, { title: fullName }]}
        actions={
          contact?.dotYouId && (
            <>
              <ActionButton
                className="m-2"
                onClick={() => navigate(`/owner/connections/${contact.dotYouId}`)}
              >
                {t('Open Connection')}
              </ActionButton>
              <ActionButton
                className="m-2"
                state={refreshState}
                onClick={() => refresh({ contact })}
                confirmOptions={{
                  title: t('Refresh data'),
                  buttonText: t('Refresh'),
                  body: t(
                    'Are you sure you want to refresh data, overwritten data cannot be recovered.'
                  ),
                }}
              >
                {t('Refresh')}
              </ActionButton>
            </>
          )
        }
      />
      {isContactsLoading ? (
        <>Loading..</>
      ) : !contact ? (
        <>Not found</>
      ) : (
        <>
          {connectionInfo?.status === 'blocked' && (
            <>
              <Alert type="critical">{t('This person has been blocked by you')}</Alert>
            </>
          )}

          <Section isOpaqueBg={true} title={t('Details')}>
            <div className="-mx-4 sm:flex sm:flex-row">
              <div className="max-h-xs mx-auto max-w-xs px-4 sm:mx-0">
                {'image' in contact && contact.image ? (
                  <figure className={'relative overflow-hidden'}>
                    <img
                      src={contact.image.url}
                      width={contact.image.pixelWidth}
                      height={contact.image.pixelHeight}
                      className="aspect-square object-cover "
                    />
                  </figure>
                ) : contact.imageFileId ? (
                  <figure className={'relative overflow-hidden'}>
                    <img src={imageUrl} className="aspect-square object-cover " />
                  </figure>
                ) : (
                  <FallbackImg
                    initials={
                      contact.name ? `${contact.name.givenName[0]}${contact.name.surname[0]}` : ''
                    }
                    className="min-w-[10rem]"
                  />
                )}
              </div>
              <div className="px-4">
                {contact.name && (
                  <p className="my-3 flex flex-row">
                    <Person className="mr-2 h-6 w-6" />
                    {contact.name.givenName ?? ''} {contact.name.surname ?? ''}
                  </p>
                )}
                <p className="my-3 flex flex-row">
                  <Phone className="mr-2 h-6 w-6" /> +12 34 56 78 90
                </p>
                <p className="my-3 flex flex-row">
                  <House className="mr-2 h-6 w-6" /> Bag End, The Shire, Middle Earth
                </p>
                <p className="my-3 flex flex-row">
                  <Cake className="mr-2 h-6 w-6" /> 22nd of September
                </p>
              </div>
            </div>
          </Section>

          {connectionInfo?.status === 'connected' && (
            <>
              <h2 className="mt-11 text-2xl">{t('Connection details')}</h2>
              <ConnectionPermissionViewer accessGrant={connectionInfo.accessGrant} />
            </>
          )}
        </>
      )}
    </>
  );
};

export default ContactDetails;
